  mounted: function(){
    this.data = this.selectData[0];
    this.initData();
  },
  methods: {
    
    deleteList(index){
      this.money  = this.money + this.bitList[index].Money;
      this.bitList.splice(index,1);
    },
    addBitList(){
      var selectIndex = document.getElementById('selectIndex').value;
      var selectValueIndex = document.getElementById('selectValueIndex').value;
      var bitNumber = document.getElementById('bitNumber').value;
      var _status = false;
      for(var key in this.bitList){
        if(this.bitList[key].NameIndex == selectIndex && this.bitList[key].ValueIndex == selectValueIndex && this.bitList[key].isGamePlay == false){
          alert("您有重複投注");
          
          _status = true;
          break;
        }else{
          _status = false;
        }
      }
      
      if(_status==false){
        var _data = {
          Name : this.bitData[selectIndex].Name,
          NameIndex : selectIndex,
          Value : this.bitData[selectIndex].List[selectValueIndex].value,
          ValueIndex :selectValueIndex,
          Odd : this.bitData[selectIndex].Odds, 
          BitNum : bitNumber, 
          Money : bitNumber*100,
          isGamePlay : false,
          isWin : null,
          number : null
        }
        this.bitList.push(_data);
        this.money = this.money - bitNumber*100;

      }

      
    },
    selectIndexChange(){
      this.selectIndex = document.getElementById("selectIndex").value;
      document.getElementById('bitNumber').value = 1; 
    },
    getColor(index){
      for(var key in this.data){
        if(this.data[key].text == index){
          if(key%2!=0){
            return "#8d1f1f";
          }else{
            return "#000";
          }
        }
      }
    },
    getSpanStyle(key){
      var styleText = '';
      if(this.data.length == 1){
          styleText = 'width:100%;height:100%;left:0;top:0;transform-origin: 50% 50%;transform: rotate(0deg) skew(0deg);';
      }else if(this.data.length == 2){
          styleText = `width:100%;height:50%;left:0;top:0;transform-origin: 50% 100%;transform: rotate(${key * 360 / this.data.length - 0}deg) skew(0deg);`;
      }else{
          styleText = `transform: rotate(${key * 360 / this.data.length - ((180 / this.data.length) - (-90 +  360 / this.data.length)) }deg) skew(${-90 +  360 / this.data.length}deg);`;
      }
      if(key == 0) styleText = styleText + 'background-color: #408600;'
      
      return styleText;
    },
    initData(){
      // 隨機順序
      this.data.sort(function(){return Math.random()>0.5?-1:1;});
      this.data.unshift({text: 0});
    },
    go(){
      var _this = this;
      this.bitListHistory = [];

      if(_this.gameStatus == false){
        _this.gameStatus = true;

        var btn = document.getElementById('triangle');
        var ball = document.getElementById('triangle-up');
        
        if(_this.oldRotate > _this.RotateTime){
            _this.oldRotate = _this.oldRotate - _this.RotateTime;
        }
        btn.style.transform = 'rotate('+_this.dataType * _this.oldRotate+'deg)';

        var style = document.createElement('style');
        style.type = 'text/css';
        var random = (Math.floor(Math.random()*360)+ 0);
        var rotate =  parseInt(random + _this.RotateTime);

        var keyFrames = `
            @keyframes zhuanzhuanzhuan{
                0%{
                    transform: rotate(${_this.dataType * _this.oldRotate}deg);
                }
                100%{
                    transform: rotate(${_this.dataType * rotate}deg); 
                }
            }
            @keyframes ballMove{
                0%{
                    bottom: 104%;
                }
                20%{
                    bottom: 120%;
                }
                80%{
                    bottom: 106%;
                }
                87%{
                    bottom: 110%;
                }
                93%{
                    bottom: 104%;
                }
                97%{
                    bottom: 106%;
                }
                100%{
                    bottom: 104%;
                }
            }
        `;
        style.innerHTML = keyFrames;
        document.getElementsByTagName('head')[0].appendChild(style);
        btn.style.animation = 'zhuanzhuanzhuan 8s forwards ease-in-out';
        ball.style.animation = 'ballMove 8s forwards ease-in-out';
        setTimeout(function(){
            btn.style.animation = '';
            _this.oldRotate = rotate;
            btn.style.transform = 'rotate('+_this.dataType * rotate+'deg)';
            document.getElementsByTagName('head')[0].removeChild(style);
            var id = Math.round((random  / (360 / _this.data.length)));
            if(id == _this.data.length){
                id = 0;
            }
            _this.number = _this.data[id].text;
            document.getElementById('outputText').innerText = _this.data[id].text;
            _this.CheckGamePlay();


            _this.gameStatus = false;
        },8000);

      }
    },
    isMatch(num,str){
      var _dd = str.split(',');
      return _dd.indexOf(num);
    },
    CheckGamePlay(){
      var _getMoney = 0;
      for(var i in this.bitList){
          // 單雙
          if(this.bitList[i].NameIndex == 9){
            if(this.bitList[i].Value == '單' && this.number%2!=0){
              _getMoney = _getMoney + this.bitList[i].BitNum * (this.bitList[i].Odd + 1) * 100;
              this.bitList[i].isWin = true;
            }
            if(this.bitList[i].Value == '雙' && this.number%2==0){
              _getMoney = _getMoney + this.bitList[i].BitNum * (this.bitList[i].Odd + 1) * 100;
              this.bitList[i].isWin = true;
            }
          }
          // 大小
          if(this.bitList[i].NameIndex == 8){
            if(this.bitList[i].ValueIndex == '0' && this.number<=18){
              _getMoney = _getMoney + this.bitList[i].BitNum * (this.bitList[i].Odd + 1) * 100;
              this.bitList[i].isWin = true;
            }
            if(this.bitList[i].ValueIndex == '1' && this.number>18){
              _getMoney = _getMoney + this.bitList[i].BitNum * (this.bitList[i].Odd + 1) * 100;
              this.bitList[i].isWin = true;
            }
          }
          // 紅黑
          if(this.bitList[i].NameIndex == 7){
            if(this.bitList[i].ValueIndex == '0' && this.getColor(this.number)!='#000'){
              _getMoney = _getMoney + this.bitList[i].BitNum * (this.bitList[i].Odd + 1) * 100;
              this.bitList[i].isWin = true;
            }
            if(this.bitList[i].ValueIndex == '1' && this.getColor(this.number)=='#000'){
              _getMoney = _getMoney + this.bitList[i].BitNum * (this.bitList[i].Odd + 1) * 100;
              this.bitList[i].isWin = true;
            }
          }

          // 排
          if(this.bitList[i].NameIndex == 6){
            if(this.isMatch(this.number,this.bitList[i].Value) != -1){
              _getMoney = _getMoney + this.bitList[i].BitNum * (this.bitList[i].Odd + 1) * 100;
              this.bitList[i].isWin = true;
            }
          }

          // 組
          if(this.bitList[i].NameIndex == 5){
            if(this.bitList[i].ValueIndex == '0' && this.number>0 && this.number<=12){
              _getMoney = _getMoney + this.bitList[i].BitNum * (this.bitList[i].Odd + 1) * 100;
              this.bitList[i].isWin = true;
            }
            if(this.bitList[i].ValueIndex == '1' && this.number>12 && this.number<=24){
              _getMoney = _getMoney + this.bitList[i].BitNum * (this.bitList[i].Odd + 1) * 100;
              this.bitList[i].isWin = true;
            }
            if(this.bitList[i].ValueIndex == '2' && this.number>24 && this.number<=36){
              _getMoney = _getMoney + this.bitList[i].BitNum * (this.bitList[i].Odd + 1) * 100;
              this.bitList[i].isWin = true;
            }
          }
          // 2~6個數字
          if(
            this.bitList[i].NameIndex == 4 ||
            this.bitList[i].NameIndex == 3 ||
            this.bitList[i].NameIndex == 2 ||
            this.bitList[i].NameIndex == 1
          ){
            if(this.isMatch(this.number,this.bitList[i].Value) != -1){
              _getMoney = _getMoney + this.bitList[i].BitNum * (this.bitList[i].Odd + 1) * 100;
              this.bitList[i].isWin = true;
            }
          }

           // 1個數字
          if(this.bitList[i].NameIndex == 0){
            if(this.number == this.bitList[i].Value){
              _getMoney = _getMoney + this.bitList[i].BitNum * (this.bitList[i].Odd + 1) * 100;
              this.bitList[i].isWin = true;
            }
          }

          this.bitListHistory.push(this.bitList[i]);
          this.bitList[i].isGamePlay = true;
          this.bitList[i].number = this.number;
      }
      this.bitList = [];
      this.money = this.money + _getMoney;
    }
  }
}
</script>

